import React from 'react';
import { Link, useHistory } from 'react-router-dom';
import Header from './Header';
import authApi from '../utils/AuthApi';

export default function Register() {
  const history = useHistory();
  const [email, setEmail] = React.useState('');
  const [password, setPassword] = React.useState('');

  function handleSubmit(e) {
    e.preventDefault();
    const data = {
      password,
      email,
    };
    authApi.signUpUser(data)
      .then((back) => {
        history.push('/sign-in');
        console.log(back);
      })
      .catch((err) => { console.log(err); });
  }

  return (
    <>
      <Header linkText="–í–æ–π—Ç–∏" link="/sign-in" />
      <section className="page-form">
        <h2 className="page-form__title">–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è</h2>
        <form className="page-form__form" onSubmit={(e) => { handleSubmit(e); }}>
          <div className="page-form__input-wrapper">
            <input
              id="email"
              className="page-form__form-item"
              type="email"
              placeholder="Email"
              name="email"
              onChange={(e) => setEmail(e.target.value)}
              required
            />
            <span id="email-error" className="popup__form-error" />
            <input
              id="password"
              className="page-form__form-item"
              type="password"
              placeholder="–ü–∞—Ä–æ–ª—å"
              name="password"
              minLength="4"
              onChange={(e) => setPassword(e.target.value)}
              required
            />
            <span id="password-error" className="popup__form-error" />
          </div>
          <div className="page-form__button-wrapper">
            <button
              className="page-form__button"
              type="submit"
              aria-label="–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è"
            >
              –ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è
            </button>
            <p className="page-form__hint-message">
              –£–∂–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã?
              <Link to="/sign-in" className="page-form__hint-link">–í–æ–π—Ç–∏</Link>
            </p>
          </div>
        </form>
      </section>
    </>
  );
}
