import React from 'react';
import PopupWithForm from './PopupWithForm';

export default function AddPlacePopup({
  isOpen, onClose, submitButtonText, onAddPlace, loadingText, isLoading,
}) {
  const [cardName, setCardName] = React.useState('');
  const [cardLink, setCardLink] = React.useState('');
  const [newCard, setNewCard] = React.useState({
    name: '',
    link: '',
  });

  function handleOnChange(e) {
    // —Å–ª–∞–±–µ–Ω—å–∫–∞—è –ø—Ä–æ–≤–µ—Ä–∫–∞, –Ω—É–∂–Ω–æ —É–ª—É—á—à–∏—Ç—å –∏–ª–∏ –∏–∑–±–∞–≤–∏—Ç—å—Å—è —Å–æ–≤—Å–µ–º
    const { target } = e;
    const { value } = target;
    const { name } = target;
    setNewCard({
      ...newCard,
      [name]: value,
    });
    target.name === 'name'
      ? setCardName(value)
      : setCardLink(value);
  }

  function handleSubmit(e) {
    e.preventDefault();
    console.log(44444);
    onAddPlace(newCard);
  }

  return (
    <PopupWithForm
      name="place"
      title="–ù–æ–≤–æ–µ –º–µ—Å—Ç–æ"
      isOpen={isOpen}
      onClose={onClose}
      onSubmit={handleSubmit}
      submitButtonText={submitButtonText}
      loadingText={loadingText}
      isLoading={isLoading}
      children={(
        <>
          <div className="popup__form-item-group">
            <input
              id="name"
              className="popup__form-item popup__form-item_input_name"
              type="text"
              placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ"
              onChange={(e) => handleOnChange(e)}
              name="name"
              minLength="1"
              maxLength="30"
              value={cardName || ''}
              required
            />
            <span id="title-error" className="popup__form-error" />
          </div>
          <div className="popup__form-item-group">
            <input
              id="link"
              className="popup__form-item popup__form-item_input_description"
              type="url"
              placeholder="–°—Å—ã–ª–∫–∞ –Ω–∞ –∫–∞—Ä—Ç–∏–Ω–∫—É"
              onChange={(e) => handleOnChange(e)}
              name="link"
              value={cardLink || ''}
              required
            />
            <span id="link-error" className="popup__form-error" />
          </div>
        </>
      )}
    />
  );
}
